import React, { Component } from 'react';

class NewForm extends Component {
    constructor(props) {
        super(props);
        this.state = {
            loading: true,
            provider : null,
            invalid : ''
        }
        this.runAPI = this.runAPI.bind(this)
    }

    runAPI(){
        const thisNPI = this.props.NPI
        const regex = /\d{10,}[a-z]*/;
        const valid = regex.test(thisNPI)
            
            if (valid){
            const myNPI= this.props.NPI
            const myToken = "3932f3b0-cfab-11dc-95ff-0800200c9a663932f3b0-cfab-11dc-95ff-0800200c9a66"
    
            var encodedURI= window.encodeURI('http://www.hipaaspace.com/api/npi/getcode?q='+ myNPI + '&token=' + myToken);
    
            fetch(encodedURI)
                .then(response => response.json())
                .then(data => {
                    this.setState({
                        provider : data,
                        loading: false
                    })
                    console.log(this.props.NPI)
                    console.log('API is working')
                    console.log(this.state.provider.NPI[0].OtherOrgName)
    
                })
                .catch(error => console.log('parsing failed', error))
    
            }
            else {
                this.setState({loading :false, invalid : 'This is an invalid identifier'})
                console.log('Invalid NPI')
            }
    }

    
    render() {
        const facName = this.state.loading ? "" : (this.state.provider.NPI[0].OrgName)
        const facOtherName = this.state.loading ? "" : (this.state.provider.NPI[0].OtherOrgName)
        const facNPI = this.state.loading ? "" : (this.state.provider.NPI[0].NPI)
        const facTaxonomy = this.state.loading ? "" : (this.state.provider.NPI[0].TaxonomyCode1)
        const text= this.state.loading ? "Loading..." : ("NPI: " + facNPI 
        + "- " + facName)
        
        return (
            <div>
                <p>Look for NPI: {this.props.NPI}</p>
                <button onClick={this.runAPI}>Search Facility</button>
                <p>Results</p><br />
                 {facName}<br />
                 {facOtherName}<br />
                 {facNPI}<br />
                 {facTaxonomy}<br />
                 
            </div>
        );
    }
}

export default NewForm;